  --boder-radius: 20px;
  --border: 1.5px solid #ec4c93;
  --border-input: 1.5px solid #c4c4c4;
  --dark-pink: #a92e71;
}
.navbar_item {
  font-family: "Poppins", sans-serif;
  max-width: 1920px;
}
.navbar_item .container {
  max-width: 1140px;
  margin-bottom: 140px;
}
.navbar_item Image {
  background: red;
  width: auto;
  height: auto;
}

.navbar_item .btn__cover__profile a {
  position: absolute;
  margin-top: -5%;
  right: 0px;
  /* margin-left: 60%; */
}
.navbar_item a.cover {
  color: #fff;
  color: var(--txt-light);
  right: 23%;
  text-decoration: none;
  border: 1px solid;
  padding: 10px 15px;
  margin-right: 35px;
  border-radius: 20px;
  border-radius: var(--boder-radius);
  border: 1.5px solid #c4c4c4;
  border: var(--border-input);
}
.navbar_item a.profile {
  color: #fff;
  color: var(--txt-light);
  text-decoration: none;
  padding: 10px 15px;
  width: auto;
  border-radius: 20px;
  border-radius: var(--boder-radius);
  border: 1.5px solid #c4c4c4;
  border: var(--border-input);
  right: 14%;
}
.navbar_item .btn__cover__profile :hover.cover {
  background: #a92e71;
  background: var(--dark-pink);
}
.navbar_item .btn__cover__profile :hover.profile {
  background: #a92e71;
  background: var(--dark-pink);
}
.navbar_item .detail__profile {
  text-align: center;
  border: 1px solid #c4c4c4;
  border-radius: 20px;
  position: absolute;
  margin-top: -8%;
  background: #fff;
  background: var(--txt-light);
  padding: 0 35px;
  height: 90%;
}
/* .view__bid {
  margin-top: 12%;
} */
.navbar_item .content__detail {
  right: 20px;
  font-size: 11px;
  margin-top: 10%;
}
.navbar_item .link__Profile b {
  font-size: 12px;
}
.navbar_item .follower_profile {
  text-decoration: none;
  background: #a92e71;
  background: var(--dark-pink);
  padding: 10px 15px;
  border-radius: 20px;
  border-radius: var(--boder-radius);
  color: #fff;
  color: var(--txt-light);
}
.navbar_item  .view__like {
  border-radius: 50%;
  border: 1.5px solid #606060;
  margin-left: 10px;
  padding: 10px 15px;
  color: #606060;
}
.navbar_item :hover.view__like {
  background: #a92e71;
  background: var(--dark-pink);
  color: #fff;
  color: var(--txt-light);
}
.navbar_item .icon__social i {
  padding: 0 10px;
  font-size: 30px;
  margin-top: 65px;
}
.navbar_item .ruler__bottom {
  border: 1px solid #c4c4c4;
  margin-top: 35%;
}
.navbar_item .bottom__date {
  color: #606060;
  font-size: 12px;
  margin-top: 30px;
}

/* css bid */
.navbar_item .top__bid {
  position: relative;
  top: 7.4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar_item .container {
  max-width: 1144px;
}

.navbar_item .top__card {
  width: 258px;
  height: 299px;
  position: relative;
  overflow: hidden;
  border-radius: 15px;
}
.navbar_item .top__card img {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: 0.5s all ease;
}

.navbar_item .top__card:hover > img {
  transform: scale(1.2);
}

.navbar_item .top__card:hover > .just__like {
  opacity: 1;
}

.navbar_item .top__card .just__like {
  opacity: 0;
  transition: 0.6s all ease;
}

.navbar_item .top__card .just__like .icon__img {
  position: absolute;
  top: 16px;
  border: none;
  right: 16px;
  width: 35px;
  height: 35px;
  background: #fff;
  border-radius: 50%;
}

.navbar_item .top__card .just__like .icon__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  height: 55%;
}

.navbar_item .title__bid h1 {
  font-size: 40px;
  font-weight: 500;
}

.navbar_item .bottom__bid {
  position: relative;
  top: 11.8rem;
}

.navbar_item .bottom__bid .slick-list {
  height: 428px;
  padding-left: 12px;
}

.navbar_item .card__bid .card__title,
.navbar_item .card__bid .card__icon,
.navbar_item .card__bid .bottom__card {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar_item .card__bid .body__card,
.navbar_item .card__bid .bottom__card,
.navbar_item .card__bid hr {
  margin-right: 22px;
}

.navbar_item .card__bid .body__card {
  position: relative;
  top: 23px;
}

.navbar_item .card__bid hr {
  position: relative;
  top: 26px;
}

.navbar_item .card__bid .card__title h5 {
  font-size: 16px;
  font-weight: 500;
  color: #000000;
}

.navbar_item .card__bid .card__icon {
  position: relative;
  top: 15px;
  align-items: inherit;
}

.navbar_item0 .card__bid {
  width: 260px !important;
}

.navbar_item .card__bid .card__icon img {
  width: 29px;
  height: 29px;
}

.navbar_item .navbar_item ..card__bid .card__icon p {
  font-size: 14px;
  font-weight: 400;
}

.navbar_item .card__icon p,
.navbar_item .bottom__card p {
  color: #606060;
}

.navbar_item .card__bid .bottom__card p {
  font-size: 12px;
  font-weight: 400;
}

.navbar_item .card__bid .bottom__card p.bt_left span {
  font-weight: 600;
  color: #000000;
}
.navbar_item .body__card .card__title p {
  display: flex;
  align-items: center;
  width: 66px;
  height: 26px;
  justify-content: center;
  border: 1.5px solid #a92e71;
  border-radius: 3px;
  font-size: 12px;
  font-weight: 600;
  color: #a92e71;
}

.navbar_item .card__bid .bottom__card {
  position: relative;
  top: 20px;
}
.navbar_item .just__like p.place {
  position: absolute;
  background: #a92e71;
  bottom: 16px;
  left: 72px;
  color: #fff;
  font-size: 13px;
  font-weight: 500;
  border-radius: 16px;
  width: 115px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar_item .just__like p.purchas {
  position: absolute;
  left: 16px;
  top: 21px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ec4c93;
  font-size: 12px;
  font-weight: 500;
  border-radius: 5px;
  width: 87px;
  height: 24px;
  color: #fff;
}

.navbar_item .just__like button:focus {
  box-shadow: none;
}
/* menuitem */

.navbar_item ..menu__item {
  width: 568px;
  height: 31px;
  display: flex;
