/* =========================== Responsive ============================= */
@media screen and (max-width: 1024px) {
  .navbar_item .container {
    max-width: 960px;
  }
  .navbar_item .menu__item {
    left: -100px;
  }

  .navbar_item .top__card {
    width: 219px;
    height: 254px;
  }

  .navbar_item .just__like p.place {
    left: 50px;
  }
  .navbar_item .detail__profile{
    position: relative;
    height: 100px;
  }
}

@media screen and (max-width: 768px) {
  .navbar_item .just__like p.place {
    left: 86px;
  }

  .navbar_item .container {
    max-width: 720px;
  }

  .navbar_item .menu__item {
    left: 0;
  }

  .navbar_item .top__card {
    width: 297px;
    height: 343px;
  }
}

@media screen and (max-width: 576px) {
  /* .just__like p.place {
    left: 90px;
  } */
  .navbar_item .container {
    max-width: 480px;
  }
  .navbar_item .menu__item {
    display: contents;
  }
  .navbar_item .detail__profile {
    position: relative;
    height: 120%;
    /* margin-top: 800px; */
    margin-left: 20px;
    left: 0px;
    margin-top:-30%;
 
  }
  .navbar_item  a.profile {
    margin-top: -240px;
  }
  .navbar_item  a.cover {
    margin-top: -241px;
    font-size: 10px;
    padding: 15px;
    right: 43% !important;
  }
  .navbar_item a.profile{
    font-size: 12px;
    margin-bottom: 20px;
  }

  .navbar_item  .bg__profile {
    margin-top: 200px;
  }
  .navbar_item .card__bid{
    /* margin-top: 420px !important; */
  }
  .navbar_item .view__img_small{
    display: flex;
  }



}
/* Ty le man hinh nho hon 320px */
@media screen and (max-width: 320px) {
  .navbar_item  .container {
    max-width: 310px;
  }
}


